                  <span class="input-text" title=""></span>
                  <span class="input-input detailinput">  {{mapForm5.DOCNO}}</span>
                </el-col>
                <el-col :span="8" class="input-item">
                  <span class="input-text" title=""></span>
                  <span class="input-input detailinput">  {{mapForm5.REGISTEREDNO}}</span>
