                <el-col :span="8" class="input-item">
                  <span class="input-text" title=""></span>
                  <span class="input-input detailinput">  {{mapForm5.FLAG=="1"?"":mapForm5.FLAG=="2"?"":mapForm5.FLAG=="3"?"":""}}</span>
                </el-col>
                <el-col :span="8" class="input-item">
                  <span class="input-text" title=""></span>
